import React, { useState, useEffect } from "react";
import Content from "./style";
import ScrollableAnchor from "react-scrollable-anchor";

function Essencia() {


    const [isMobile, setMobile] = useState(false);

    useEffect(() => {
        if (window.innerWidth >= 992) {
            setMobile(false);
        } else {
            setMobile(true);
        }
    }, [window.innerWidth]);

    return (
        <ScrollableAnchor id="terrazul">
            {isMobile ? '' :
                <Content>
                    <div className="title justify-content-center">Nossa essncia</div>
                    <div className="topo row d-flex justify-content-center offset-sm-1">
                        <div className="textos col-6 justify-content-center mt-2">
                            <div className="text">
                                Somos movidos por <strong>planejar sonhos</strong> e <strong>transformar vidas.</strong> So <br />
                                <strong>mais de 5.000 lares e empresas impactadas com a nossa entrega.</strong> <br />
                                O <strong>relacionamento</strong> e <strong>transparncia</strong> so parte do que chamamos <br />
                                de comprometimento com o cliente. Misso essa que <strong></strong>motiva e <br />
                                <strong>impulsiona</strong> toda a nossa equipe a buscar por inovaes e as <br />
                                melhores formas de construir sonhos que se tornaro realidade.
                                <p />
                                Com <strong>mais de 10 anos de mercado,</strong> estamos criando narrativas e <br />
                                dando continuidade a tantas outras. Histrias e pessoas que <br />
                                acreditam em nosso trabalho e confiam que juntos podemos <br />
                                construir uma sociedade ainda melhor.

                            </div>
                        </div>

                        <div className="text">
                            Fomos uma das primeiras incorporadoras do estado e hoje <br />
                            estamos sediados em Rio Claro|SP com atuao em diversas <br />
                            cidades do interior paulista.
                            <p />
                            Est na nossa essncia construir alm de lares! Dedicamos para <br />
                            entregar produtos com <strong>qualidade de vida, bem-estar</strong> e <br />
                            <strong>inovao</strong> para cada cliente. So <strong>empreendimentos projetados</strong> <br />
                            para terem diferenciais e a melhor qualidade para o que<br />
                            importa...VOC!
                            <p />
                            Aqui na Barbo, trabalhamos com <strong>tica</strong> e <strong>responsabilidade,</strong> e <br />
                            por isso, temos orgulho de participar de <strong>momentos</strong> <br />
                            <strong>inesquecveis</strong> da sua vida!
                        </div>
                    </div>

                </Content>
            }
        </ScrollableAnchor>
    );
}

export default Essencia;
